/** @jsx jsx */
import { jsx } from '@emotion/core';
import styled from '@emotion/styled';
import { Fragment } from 'react';
import { Member } from 'src/models';
import Card from 'src/components/Card';
import { Color } from 'src/constants';
import AnimatedListItem from 'src/components/Animated/AnimatedListItem';

interface Props {
  member: Member;
}
const MemberCard = ({ member }: Props) => {
  return (
    <AnimatedListItem>
      <Card>
        <Card.Img src={member.profileImg} />
        <Card.Body>
          <Card.Title>{member.name}</Card.Title>
          <Card.Subtitle>
            {member.role}
            {member.company ? ` / ${member.company}` : ''}
          </Card.Subtitle>
          <Links>
            <Fragment>
              {member.blog ? (
                <LinkIcon>
                  <a href={member.blog} target="_blank" rel="noopener noreferrer">
                    <i className="fas fa-blog"></i>
                  </a>
                </LinkIcon>
              ) : null}
              {member.github ? (
                <LinkIcon>
                  <a href={member.github} target="_blank" rel="noopener noreferrer">
                    <i className="fab fa-github"></i>
                  </a>
                </LinkIcon>
              ) : null}
              {member.linkedin ? (
                <LinkIcon>
                  <a href={member.linkedin} target="_blank" rel="noopener noreferrer">
                    <i className="fab fa-linkedin"></i>
                  </a>
                </LinkIcon>
              ) : null}
            </Fragment>
          </Links>
        </Card.Body>
      </Card>
    </AnimatedListItem>
  );
};

const Links = styled.ul`
  margin: 1rem 0 0 0;
  padding: 0;
`;
const LinkIcon = styled.li`
  list-style: none;
  display: inline-block;
  margin-right: 1rem;
  margin-bottom: 0;
  &:last-child {
    margin-right: 0;
  }
  i {
    color: ${Color.Grey600};
    transition: transform 0.2s ease-in-out, opacity 0.2s ease-in-out;
    &:hover {
      transform: translate(0, -5px);
      opacity: 0.8;
    }
  }
`;

export default MemberCard;
