/** @jsx jsx */
import { jsx } from '@emotion/core';
import { Card as BootstrapCard } from 'react-bootstrap';
import { ReactNode } from 'react';
import styled from '@emotion/styled';
import { Color } from 'src/constants';

interface Props {
  children?: ReactNode;
}

const Card = ({ children }: Props) => {
  return <StyledCard>{children}</StyledCard>;
};

const StyledCard = styled(BootstrapCard)`
  margin-bottom: 1rem;
  color: ${Color.Grey900};
  border: none;
  border-radius: 1rem;
  box-shadow: 0 6px 20px 0 rgba(0, 0, 0, 0.1);
  overflow: hidden;
`;

const Img = styled(BootstrapCard.Img)`
  border-top: {
    right-radius: calc(1rem - 1px);
    left-radius: calc(1rem - 1px);
  }
`;

const Title = styled(BootstrapCard.Title)`
  font-weight: bold;
  font-size: 1rem;
`;

const Subtitle = styled(BootstrapCard.Subtitle)`
  margin-top: 0.5rem;
  font-size: 0.8rem;
  img {
    display: inline-block;
    width: 1.5rem;
    margin: 0 0.5rem 0 0;
    border-radius: 6px;
  }
`;

const Text = styled(BootstrapCard.Text)`
  border-top: 1px solid #000;
  margin-top: 1rem;
  margin-bottom: 0.5rem;
  padding-top: 1rem;
  font-size: 0.8rem;
`;

Card.Img = Img;
Card.Body = BootstrapCard.Body;
Card.Title = Title;
Card.Subtitle = Subtitle;
Card.Text = Text;

export default Card;
